<template>
  <div><!-- 跟标签 -->
    <div class="mask_layer" id="mask_layer" v-show="price_mask_isshow"></div>
    <!-- 头部,标签可以用大写,不太符合w3c的标准 -->
    <!-- <NavHeader/> -->
    <nav-header/>
    <!-- 面包屑,导航状态 -->
    <nav-bread>
      <span>Goods</span>
      <!-- <span slot='n'>匹配n的插槽</span>
      <span slot='p'>匹配p的插槽</span> -->
    </nav-bread>
    <!-- 商品列表 -->
    <div class="accessory-result-page accessory-page">
      <div class="container">
        <div class="filter-nav">
          <span class="sortby">Sort by:</span>
          <a href="javascript:void(0)" class="default" :class="{cur:curs=='default'?true:false}" @click="defaults">Default</a>
          <a href="javascript:void(0)" class="price" @click="sortGoods()" :class="{cur:curs=='price'?true:false}">Price <svg class="icon icon-arrow-short">
              <use xlink:href="#icon-arrow-short"></use>
            </svg></a>
          <a href="javascript:void(0)" class="filterby stopPop" @click="price_menu">Filter by</a>
        </div>
        <div class="accessory-result">
          <!-- filter -->
          <div class="filter stopPop" id="filter" v-bind:class="{'filterby-show':price_menu_isshow}">
            <dl class="filter-price">
              <dt>Price:</dt>
              <dd>
                <a href="javascript:void(0)" v-bind:class="{'cur':priceState_all=='active'}" @click="pricestate_set('active')">All</a>
              </dd>
              <dd v-for="(item,index) in priceFilter" :key="index">
                <a href="javascript:void(0)" v-bind:class="{'cur':priceState_all==index}" @click="pricestate_set(index)">{{item.startPrice}} - {{item.endPrice}}</a>
              </dd>
            </dl>
          </div>

          <!-- search result accessories list -->
          <div class="accessory-list-wrap">
            <div class="accessory-list col-4">
              <ul>
                <li v-for="(item,index) in goodlist" :key="index">
                  <div class="pic">
                    <a href="#"><img v-lazy="/static/+item.productImage" alt=""></a>
                  </div>
                  <div class="main">
                    <div class="name">{{item.productName}}</div>
                    <div class="price">{{item.salePrice}}</div>
                    <div class="btn-area">
                      <a href="javascript:;" class="btn btn--m" @click="addCart(item.productId)">加入购物车</a>
                    </div>
                  </div>
                </li>
              </ul>
              <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="300" class="goods_load">
                <img src="../../static/loading-svg/loading-balls.svg" alt="" v-show="loadingSVG">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 页面尾部 -->
    <!-- 插槽 -->
    <Modal v-bind:mdShow="mdShow" v-on:close="closeModal">
      <p slot="message">请先登录,再进行添加购物车!!!</p>
      <div slot="btnGroup">
        <a class="btn btn--m" @click="closeModal">关闭</a>
      </div>
    </Modal>
    <!-- 插槽 -->
    <Modal v-bind:mdShow="mdShowCart" v-on:close="closeModal">
      <p slot="message">添加成功!!!</p>
      <div slot="btnGroup">
        <a class="btn btn--m" @click="mdShowCart = false">继续购物</a>
        <router-link class="btn btn--m" to="/cart">查看购物车</router-link>
      </div>
    </Modal>
    <NavFooter/>
  </div>
</template>

<script>
  // @指向了src
  import '@/assets/css/base.css';
  import '@/assets/css/product.css';
  import '@/assets/css/login.css';
  import '@/assets/css/checkout.css';
  import NavHeader from '@/components/NavHeader.vue';
  import NavFooter from '@/components/NavFooter.vue';
  import NavBread from '@/components/NavBread.vue';
  import axios from 'axios';//导入axios
  import Modal from '@/components/Modal.vue'; //导入模态框
  export default{
    data(){
      return {
        mdShowCart:false,//默认-登录框为隐藏
        mdShow:false,//默认-没有登录Modal为隐藏
        curs:'default',//选中状态,默认default显示选中状态
        loadingSVG:false,//加载动画默认隐藏
        busy:true,//分页下拉加载,true禁止加载,false可以加载
        sortFlag:true,//升序开关
        page:1,//初始化从第一页开始
        pageSize:4,//每页显示的条数

        price_menu_isshow:false,//菜单是否显示
        price_mask_isshow:false,//遮罩层是否显示
        goodlist:[], //用来存放商品列表数据
        priceFilter:[
          {startPrice:"0.00",endPrice:"100.00"},
          {startPrice:"100.00",endPrice:"300.00"},
          {startPrice:"300.00",endPrice:"500.00"},
          {startPrice:"500.00",endPrice:"1000.00"}
        ],
        priceState_all:"active",//绑定class
      }
    },
    mounted:function(){//生命周期
      this.getGoodslist(undefined,'def');//调用方法,方法里去请求商品列表数据
    },
    methods:{
      closeModal(){//关闭模态框
        this.mdShowCart=false
        this.mdShow=false
      },
      addCart(productId){//购物车
        axios.post('/goods/addCart',{
          productId:productId
        }).then(
          res=>{
            // console.log(res.data.status)
            // alert(res.data.status=='0'?'添加成功': `添加失败:${res.data.mes}`)
            // res.data.status=='0'?this.mdShowCart=true:this.mdShow=true
            // 当mdShow为true,这个变量会绑定到Modal上,会传递到Modal中
            if(res.data.status=='0'){
              this.mdShowCart=true
              this.$store.commit('updateCartCount',1)
            }else{
              this.mdShow=true
            }
          }
        )
      },
      loadMore(){
        setTimeout(()=>{
          this.page++ //当你向下滚动时,就会页面加1
          if(this.curs=='price'){//排序
            this.getGoodslist(true);//调用方法,方法里去请求商品列表数据
            //true决定是否累计,也就是向下滚动鼠标的时候是否保存上面的图片
          }else{//不排序
            this.getGoodslist(true,'def')
          }
        },1000)
      },
      pricestate_set(index){
        this.priceState_all=index;
        this.price_menu_isshow=false;
        this.price_mask_isshow=false;
        this.page=1 //保证从第一页开始
        if(this.curs=='price'){//排序
          this.getGoodslist();//调用方法,方法里去请求商品列表数据
          //true决定是否累计,也就是向下滚动鼠标的时候是否保存上面的图片
        }else{//不排序
          this.getGoodslist(undefined,'def')
        }
      },
      price_menu(){
        this.price_menu_isshow=true;//菜单显示
        this.price_mask_isshow=true;//遮罩层显示
      },
      getGoodslist(flag,sta){//请求商品列表数据,初始化触发(undefined),是否累加取决于flag
        this.loadingSVG=true;//调用方法时启动加载动画
        var param={//定义渲染产品列表的参数,默认从第一页开始
          priceLevel: this.priceState_all, //传递给后台点击的 数据过滤
          page:this.page,
          pageSize:this.pageSize,
          sort:this.sortFlag?1:-1 ,//1升序,-1降序,系统定的
        }
        if(sta=='def'){
          param.sort='def'
        }
        axios.get('/goods/list',{params:param}).then(
          res=>{
            // console.log(res);
            if(res.data.status==0){
              this.loadingSVG=false;//加载动画隐藏
              if(flag){//如果没有滑动鼠标,这个变量是undefined,就会运行else
                this.goodlist=this.goodlist.concat(res.data.result.list)
                // express后台的数据为0,说明没有数据了
                if(res.data.result.count==0){
                  this.busy=true //禁止加载
                }else{
                  this.busy=false //可以加载
                }
              }else{
                this.goodlist=res.data.result.list;
                this.busy=false //可以加载
              }
            }else{
               this.goodlist=[];
            }
          }
        )
      },
      sortGoods(){
        this.curs="price"
        this.sortFlag=!this.sortFlag //实现升降序切换
        this.page=1 //点完升降序,应该从第一页开始
        this.getGoodslist();//调用方法,方法里去请求商品列表数据
      },
      defaults(){
        this.curs="default"
        this.page=1
        this.getGoodslist(undefined,'def');
      }
    },
    components:{//注册组件
      NavHeader,
      NavFooter,
      NavBread,
      Modal,
    },
  }
</script>

<style>
  .mask_layer{
    width:100vw;
    height: 100%;
    background: rgba(0,0,0,0.3);
    position: absolute;
    z-index: 10;
  }
  .goods_load{
    height: 100px;
    text-align: center;
    line-height: 100px;
  }
</style>



