// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
// vuex是全局使用,所有在main.js中引用
import Vuex from 'Vuex' //引入Vuex
Vue.use(Vuex) //挂载使用
import App from './App'
import router from './router'
import infiniteScroll from 'vue-infinite-scroll'//引用分页插件
import lazyload from 'vue-lazyload'//导入懒加载插件
// 过滤器使用,导入它  全局引用
import {currency} from './util/currency.js' //函数用{}导入
Vue.filter('currency',currency) //挂载
Vue.use(lazyload,{ //挂载
  loading:"/static/loading-svg/loading-spinning-bubbles.svg"
})
const store=new Vuex.Store({
  state:{
    nickName:'',//登录名称
    cartCount:0,//购物车数量
  },
  mutations:{
    updateUserInfo(state,nickName){//把state传递过来的值修改成我们接收的值
      state.nickName=nickName
    },
    updateCartCount(state,cartCount){//更改购物车数量
      state.cartCount+=cartCount
    },
    initCartCount(state,cartCount){//初始化使用
      state.cartCount=cartCount
    }
  }
})
Vue.config.productionTip = false
Vue.use(infiniteScroll)//使用分页插件
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,//将store注册到实例中
  components: { App },
  template: '<App/>'
})



