import Vue from 'vue'
import Router from 'vue-router'
import Goodslist from '@/views/Goodslist'
import Cart from '@/views/Cart.vue' //引入购物车组件
import Address from '@/views/Address.vue' //引入地址组件
import OrderConfirm from '@/views/orderConfirm.vue' //引入订单组件
import OrderSuccess from '@/views/orderSuccess.vue' //引入订单成功组件

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Goodslist',
      component: Goodslist
    },
    {
      path: '/cart',
      // name: 'Cart',
      component: Cart
    },
    {
      path: '/address',
      // name: 'Cart',
      component: Address
    },
    {
      path: '/orderConfirm',
      // name: 'Cart',
      component: OrderConfirm
    },
    {
      path: '/orderSuccess',
      // name: 'Cart',
      component: OrderSuccess
    }
  ]
})



